.slide {
    .wrap-heading {
        margin-bottom: 4px;
    }

    .title {
        @include title();
    }

    .sub-title {
        margin-top: 4px;
        overflow-x: scroll;
        display: flex;
        align-items: center;

        // INFO :  style for scroll
        &::-webkit-scrollbar-track {
            -webkit-box-shadow: inset 0 0 2px $white;
            border-radius: 6px;
            background-color: $white;
        }

        &::-webkit-scrollbar {
            width: 2px;
            height: 4px;
        }

        &::-webkit-scrollbar-thumb {
            border-radius: 6px;
            background-color: $border-color;
        }

        >* {
            font-weight: $font-weight-normal;
            font-size: 15px;
            line-height: 24px;
            display: flex;
            align-items: flex-end;
            text-transform: uppercase;
            color: #343A40;
            padding-left: 15px;
            background: url(../img/sao.png) left top 7px no-repeat;
            margin-left: 16px;
            white-space: nowrap;

            &:first-child {
                margin-left: 0;
            }
        }
    }

    .box-content {
        .story {
            margin: 0 5px;
            position: relative;
            opacity: 0.2;
            transition: all 0.5s;

            &__thumb {
                width: 200px;
                height: 300px;
                border-radius: 6px;
                position: relative;

                a {
                    position: relative;

                    &::before {
                        position: absolute;
                        content: "";
                        width: 100%;
                        height: 100%;
                        background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.7) 100%);
                    }
                }

            }

            &__heading {
                position: absolute;
                bottom: 37px;
                padding: 0 10px;

                a {
                    font-size: 16px;
                    text-transform: uppercase;
                    line-height: 23px;
                    color: #FFFFFF;
                    font-weight: $font-weight-normal;

                }
            }

            &__meta {
                position: absolute;
                bottom: 13px;
                padding: 0 10px;

                .time {
                    font-size: 13px;
                    line-height: 24px;
                    color: #ADB5BD;
                    font-weight: $font-weight-medium;
                }
            }

            &.slick-active {
                opacity: 1;
            }
        }
    }
}